.modal__space {
    padding: 3px 0px 30px 0px;
    /* сверху | справа | снизу | слева */
    position: fixed;
    background-color: rgba(28, 28, 33, 1);
    border-radius: 40px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    width: 720px;
    max-height: 718px;
    max-width: 100%;
    text-align: center;
    box-shadow: rgba(76, 76, 255, 0.5) -5px 5px 250px 90px;
    border: 2px solid #4c4cff;
    transition: visibility 0.6s 0s, opacity 0.6s 0s linear;

}

.modalBody {
    padding: 15px;
    display: flex;
    flex-direction: column;
}

.modal__close_icon {
    background-image: url(../../images/close.svg);
    position: absolute;
    top: 20px;
    right: 43px;
    cursor: pointer;
    z-index: 1;
    width: 18px;
    height: 18px;

}